/* Color schemes */
.demo-1 {
  --color-text: #86eae7;
  --color-link: #fff;
  --color-link-hover: #86eae7;
  --color-title: #fff; }

.demo-2 {
  --color-text: #ff53ff;
  --color-link: #a3daff;
  --color-link-hover: #fff;
  --color-title: #fff; }

.demo-3 {
  --color-text: #1ed97c;
