a {
  color: var(--color-link);
  outline: none;
  text-decoration: none; }

a:hover,
a:focus {
  color: var(--color-link-hover);
  outline: none; }

.frame {
  position: relative;
  /* padding: 3rem 5vw; */
  text-align: center;
  z-index: 1000; }

.frame__title {
  font-size: 1rem;
  font-weight: normal;
  margin: 0 0 1rem; }

.frame__links {
  display: inline; }

.frame__github,
.frame__links a:not(:last-child),
.frame__demos a:not(:last-child) {
  margin-right: 1rem; }

.frame__demos {
  margin: 1rem 0; }

.frame__demo--current,
.frame__demo--current:hover {
  color: var(--color-link);
  text-decoration: underline; }

.content {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: flex-start;
  position: relative;
  width: 100vw; }

.content__title {
  color: var(--color-title);
  font-size: 10vw;
  left: 0;
  line-height: 1;
  margin: -5.5vw 0 0 0;
  position: absolute;
  text-align: center;
  top: 50%;
  width: 100%;
  z-index: 100; }

@media screen and (min-width: 53em) {
  .message {
    display: none; }
  .frame {
    align-content: space-between;
    display: grid;
    grid-template-areas: 'title title ... ' '... ... ...' 'github demos links';
    grid-template-columns: 25% 50% 25%;
    grid-template-rows: auto auto auto;
    height: 100vh;
    left: 0;
    max-width: none;
    padding: 2rem 2.25rem;
    pointer-events: none;
    position: fixed;
    text-align: left;
    width: 100%;
    z-index: 10000; }
  .frame__title-wrap {
    display: flex;
    grid-area: title; }
  .frame__title {
    margin: 0; }
  .frame__github {
    grid-area: github;
    justify-self: start;
    margin: 0; }
  .frame__demos {
    grid-area: demos;
    justify-self: center;
    margin: 0; }
  .frame__links {
    grid-area: links;
    justify-self: end;
    padding: 0; }
  .frame a {
    pointer-events: auto; }
  .content {
    height: 100vh;
    justify-content: center; } }

