.CherryPick {
  /* fallback for old browsers */
  background: #dc281e;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#df3026), to(#dc281e));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, #df3026, #dc281e);

  .technologies-container {
    .technologies {
      div {
        div {
          -webkit-filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.5));
        }

        &.typescript {
          background-image: url('#{$portfolio-img-path}/cheryPick/typescript.png');
        }

        &.laravel {
          background-image: url('#{$portfolio-img-path}/cheryPick/laravel.png');
        }

        &.mysql {
          background-image: url('#{$portfolio-img-path}/cheryPick/mysql.png');
        }

        &.php {
          background-image: url('#{$portfolio-img-path}/cheryPick/php.png');
        }

        &.js {
          background-image: url('#{$portfolio-img-path}/cheryPick/js.png');
        }
      }
    }

    .description {
      margin-bottom: 0;

      p {
        color: #000;
        font-family: 'Hammersmith One', sans-serif;
        font-size: 18px;
        margin-bottom: 10px;
        margin-top: 10px;
        max-width: 600px;
        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
      }

      a {
        color: #000;
        text-decoration: none;
      }
    }

    .rotate_button {
      display: none;
    }
  }

  .logo-container {
    align-items: center;
    -webkit-box-align: center;
    -webkit-box-direction: normal;
    -webkit-box-orient: vertical;
    -webkit-box-pack: center;
    display: -webkit-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

    img {
      width: 200px;
    }

    .header {
      color: #000;
      font-family: 'Open Sans', 'Condensed';
      font-size: 50px;
      font-weight: 600;
      margin-bottom: 10px;
      text-shadow: rgba(0, 0, 0, 0.5) 5px 5px 8px;
      text-transform: uppercase;
      z-index: 100;
    }

    .paragraph {
      color: #000;
      display: inline-block;
      font-family: 'Hammersmith One', sans-serif;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 0.62px;
      line-height: 30px;
      z-index: 100;
    }
  }

  .arrow {
    background-position: center;
    background-size: 80px;
    cursor: pointer;
    display: block;
    height: 80px;
    margin: auto;
    position: relative;
    width: 80px;
  }

  .show {
    background-image: url('#{$portfolio-img-path}/down-arrow.svg');
  }

  .hide {
    background-image: url('#{$portfolio-img-path}/cancel.svg');
  }
}

@media (max-width: 500px) {
  .CherryPick {
    .logo-container {
      .header {
        font-size: 35px;
        padding-right: 25px;
      }

      img {
        width: 150px;
      }
    }

    .technologies-container {
      .technologies {
        -webkit-box-direction: normal;
        -webkit-box-orient: vertical;
        flex-direction: column;
        margin-bottom: 10px;

        div div {
          background-size: 50px !important;
          height: 50px !important;
          width: 50px !important;
        }
      }
    }
  }
}
